.Tab-container {
  margin: 40px auto;
  width: 61.8%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  /* margin: auto; */

}

.Selected-tab {
  font-weight: bolder;
}

.Top-logo-container {
  font-size: 24px;
  font-weight: bolder;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
  color: black;
}

.Top-logo-container:active {
  color: #666;
}

.Top-logo-container:visited {
  color: initial;
}

/* .Top-logo-container svg .cls-1 {
  fill: white
} */

.Top-logo-container:active {
  color: #666;
}

.Top-logo-container.Top {
  color: white;
}

.Top-logo-container svg {
  margin-right: 12px;
  height: 24px;
  fill: black;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
}

.Top-logo-container.Top svg {
  fill: white
}


.Tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /* flex: 0.5; */
  /* justify-content: space-evenly */
}

.Tab {
  font-size: 24px;
  font-weight: normal;
  color: #aaa;
  cursor: pointer;
  -webkit-transition: ease-in-out 0.2s;
  -o-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
  margin-left: 24px;
  opacity: 0.6;
}

.Tab.Top {
  color: white;
}


.Tab.active {
  /* font-size: 24px; */
  /* font-weight: bolder; */
  opacity: 1;
  /* color: black;
  cursor: pointer;
  transition: ease-in-out 0.5s;
  margin-left: 20px; */
}



.Tab:hover {
  opacity: 1;
}

.Tab.Top:hover {
  opacity: 1;
}

.Logo {
  height: 24px;
  margin-right: 12px;
}

.Tab-chevron {
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
}

.Tab-chevron.Collapsed {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

.Tab-chevron.Top {
  color: white;
}

.Tab-toggle {
  font-size: 24px;
  font-weight: normal;
  color: #666;
  cursor: pointer;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
  margin-left: 20px;
  /* display: inline; */
  /* display: flex; */
}

.Tab-toggle-text.Top {
  color: #fff;
}

@media screen and (max-width: 768px) {
  .Tab-container {
    margin: 20px;
    width: calc(100% - 40px);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -webkit-transition: ease-in-out 0.2s, height 0.5s;
    -o-transition: ease-in-out 0.2s, height 0.5s;
    transition: ease-in-out 0.2s, height 0.5s;
    /* -webkit-transition: ease-in-out 0.2s;  */
  }

  .Top-logo-container {
    font-size: 21px;
    font-weight: bolder;
    -ms-flex-direction: row;
        flex-direction: row;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }

  .Logo {
    height: 21px;
    margin-right: 12px;
  }

  .Tabs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 0;
    /* width: 100%; */
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: end;
        align-items: flex-end;
    -webkit-transition: ease-in-out 0.2s, height 0.5s;
    -o-transition: ease-in-out 0.2s, height 0.5s;
    transition: ease-in-out 0.2s, height 0.5s;
  }

  .Tab {
    margin: 20px 0 0 0;
    -webkit-transition: ease-in-out 0.2s;
    -o-transition: ease-in-out 0.2s;
    transition: ease-in-out 0.2s;
    /* transition-delay: 0.2s; */
    font-size: 21px
  }

  .Tab-toggle {
    font-size: 21px;
  }
  

  .Top-logo-container.Top {
    color: white;
  }
  
  .Top-logo-container svg {
    margin-right: 8px;
    height: 21px;
    fill: black;
    -webkit-transition: ease-in-out 0.5s;
    -o-transition: ease-in-out 0.5s;
    transition: ease-in-out 0.5s;
  }
  
  .Top-logo-container.Top svg {
    fill: white
  }
}



